  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

body {
  font-family: "Quicksand", sans-serif;
}

body .wrapper {
  height: 100vh;
  background-color: #3f3f3f;
}

body .wrapper .header_container {
  position: fixed;
  width: 100%;
}

body .wrapper .header_container header {
  height: 80px;
  background-color: #3f3f3f;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 40px 0 40px;
}

body .wrapper .header_container header #title_container {
  display: flex;
  align-items: center;
}

body .wrapper .header_container header #title_container #icon_title {
  width: 33px;
}

body .wrapper .header_container header #title_container h1 {
  font-family: "Special Elite", cursive;
  font-size: 42px;
  padding-top: 8px;
  margin: 0px;
  color: white;
}

body .wrapper .header_container header .search_container {
  display: flex;
  align-items: center;
}

body .wrapper .header_container header .search_container input {
  border-radius: 2rem;
  height: 45px;
  width: 330px;
  margin-right: 10px;
  padding: 10px;
}

body .wrapper .header_container header .search_container button {
  border-radius: 2rem;
  display: inline-block;
  width: 45px;
  height: 45px;
  border-bottom: none;
  cursor: pointer;
  color: #259179;
}

body .wrapper .header_container header .search_container button .icon_search {
  font-size: 22px;
}

body .wrapper .header_container .aside_container {
  background-color: #259179;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0 10px 0 10px;
}

body .wrapper .header_container .aside_container .icon_fav {
  font-size: 18px;
  color: white;
}

body .wrapper .header_container .aside_container #favourites_list {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
}

body .wrapper .header_container .aside_container #favourites_list .listSeries {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  background: white;
  border-radius: 20px;
  margin: 15px;
}

body .wrapper .header_container .aside_container #favourites_list .listSeries img {
  width: 39px;
  border-radius: 46px;
}

body .wrapper .header_container .aside_container #favourites_list .listSeries .main-title {
  display: inline;
  font-size: 15px;
  padding: 8px;
}

body .wrapper .header_container .aside_container #favourites_list .listSeries .deleteButton {
  border-radius: 50%;
  font-size: 14px;
  font-weight: 200;
  height: 18px;
  width: 18px;
  background-color: #259179;
  color: white;
  margin-right: 8px;
  padding-bottom: 6px;
  border: none;
  cursor: pointer;
}

body .wrapper .main_container {
  height: auto;
  background-color: #3f3f3f;
}

body .wrapper .main_container ul {
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: stretch;
  margin: 190px 60px 0 60px;
}

body .wrapper .main_container ul .listSeries {
  width: 247px;
  height: 370px;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px;
  border-radius: 32px;
  cursor: pointer;
}

body .wrapper .main_container ul .listSeries img {
  border-radius: 21px;
  margin-bottom: 12px;
}

body .wrapper .main_container ul .listSeries .main-title {
  font-size: 20px;
  font-weight: 600;
}

body .wrapper .main_container ul .favouritesMainStyle {
  background-color: #259179;
  color: white;
}

body .wrapper .main_container ul .icon_fav {
  margin-top: 3px;
  font-size: 10px;
  color: white;
}
